import React from 'react';
import Latex from 'react-latex';
export default function Posto(){ return(<>
<h2>
    Posto
</h2>

Posto(Rank) de uma matriz <Latex>{"$A^{mxn},m\\leq n$"}</Latex> é o número de linhas linearmente independentes denotado por r(A). Uma matriz é chamada de posto completo se, todas as suas linhas são linearmente independentes.

<h3>
    Propriedades
</h3>

<ul>
    <li>
        <Latex>{"$r(A)=r(A^T)=r(A^tA)=r(AA^T)$"}</Latex>
    </li>
    <li>
        <Latex>{"$r(AB)\\leq min\\{r(A),r(B)\\}$"}</Latex>
    </li>
    <li>
        <Latex>{"$r(AB)=R(A)\\text{, se }B^{nxn}$"}</Latex>
    </li>
    <li>
        <Latex>{"$r(A + B) \\leq r(A) + r(B)$"}</Latex>
    </li>
</ul>

</>)}